@charset "UTF-8";
/*----------------------------------------------------------------------------
* ページレイアウト
----------------------------------------------------------------------------*/
#wrapper {
  height: 3000px;
}

.container {
  max-width: 1300px;
  margin-right: auto;
  margin-left: auto;
}
.container .content_width {
  width: calc(100% - 40px);
  margin-right: auto;
  margin-left: auto;
}

.area_header_content {
  padding: 1em 0;
}

/*----------------------------------------------------------------------------
* グローバルメニュー
----------------------------------------------------------------------------*/
/*--------------------------------------
* module variables
--------------------------------------*/
/*--------------------------------------
* modules
--------------------------------------*/
/*
* @see plugin/css/modules/_gm_accordion.scss */
.gm_area .gm_inner {
  border-top: 1px solid #000;
  border-bottom: 1px solid #000;
  background-color: #fff;
}

/*
* @see plugin/css/modules/_gm_nav.scss */
.gm_area .gm_nav > .gm_nav_item {
  flex: 1 1 auto;
}
.gm_area .gm_nav > .gm_nav_item > .gm_item_name {
  text-align: center;
  padding: 1.5em 0;
}
.gm_area .gm_nav > .gm_nav_item:not(:last-child) {
  border-right: 1px dotted #000;
}

/*
* @see plugin/css/modules/_gm_accordion.scss */
.gm_area .gm_nav .gm_accordion .gm_accordion_body {
  background-color: #ccc;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.26);
}
.gm_area .gm_nav .gm_accordion .gm_accordion_body li a {
  padding: 10px;
  background-color: #ccc;
}
.gm_area .gm_nav .gm_accordion .gm_accordion_body li a:hover {
  background-color: #eee;
}
.gm_area .gm_nav .gm_accordion .gm_accordion_body li:not(:last-child) a {
  border-bottom: 1px dotted #000;
}

/*
* @see plugin/css/modules/_gm_mega.scss */
.gm_area .gm_mega .gm_mega_body {
  background-color: #ccc;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.26);
  padding: 1em 0;
}

/*
* @see plugin/css/modules/_gm_position_fix.scss */
.gm_area .gm_inner.is_fixed {
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.26);
}
.gm_area .gm_inner.is_fixed .gm_nav .gm_nav_item .gm_item_name {
  padding: 1em 0;
}
.gm_area .gm_inner.is_scroll_hide {
  box-shadow: none;
}